<template>
  <div id="projectDetailsPage" v-loading="" class="container fluid">

    <h3>Projects</h3>
    <br />

    <el-tabs type="border-card">
      <el-tab-pane class="projectContainer" v-for="projectItem in projectList">
        <!-- Tab Header -->
        <span slot="label">{{projectItem.name}}</span>

        <form>

          <!-- Project Name -->
          <div class="form-group" v-loading="updatingProjectName">
            <label for="exampleInputEmail1">Project Name</label><br />
            <input type="text" class="form-control form-control-sm" v-model="projectItem.name" placeholder="Please Enter Project Name"
              style="width: 300px; display: inline-block; margin-right: 10px;" :disabled="projectNameMode == 'normal'">

            <i v-if="projectNameMode == 'normal'" @click="projectNameMode = 'edit';" class="fa fa-pencil" style="cursor:pointer; color: #409eff;"></i>
            <span v-else>
              <el-button size="mini" type="primary" @click="onUpdateProjectName(projectItem)" plain>Save</el-button>
              <el-button size="mini" type="danger" plain @click="projectItem.name = projectItem.originalName; projectNameMode = 'normal';">Cancel</el-button>
            </span>
          </div>

          <!-- Members -->
          <div class="form-group" style="margin-top: 35px;">
            <label for="exampleInputPassword1">Members</label>

            <!-- Add Member -->
            <div class="addUserBox row" v-loading="addingNewUser">

              <div class="col-md-12" style="color: red; text-align: center; margin-bottom: 15px;" v-if="newUserErrorMsg != null">
                {{newUserErrorMsg}}
              </div>

              <!-- First Name -->
              <div class="col-md-3 inputContainer">
                First Name <input type="text" class="form-control form-control-sm" v-model="newUserInfo.first_name" placeholder="Enter First Name"
                  required>
              </div>

              <!-- Last Name -->
              <div class="col-md-3 inputContainer">
                Last Name<input type="text" class="form-control form-control-sm" placeholder="Enter Last Name" v-model="newUserInfo.last_name"
                  required>
              </div>

              <!-- Email Id -->
              <div class="col-md-3 inputContainer">
                Email<input type="email" v-model="newUserInfo.email" class="form-control form-control-sm" placeholder="Enter Email Id"
                  required>
              </div>

              <!-- Role -->
              <div class="col-md-2 inputContainer">
                Role<br />
                <el-radio-group v-model="newUserInfo.role" size="mini">
                  <el-radio-button label="user">User</el-radio-button>
                  <el-radio-button label="admin">Admin</el-radio-button>
                </el-radio-group>
              </div>
              <div class="col-md-1 inputContainer">
                <br />
                <el-button type="success" size="mini" @click="addNewUserToProject(projectItem)">Add User</el-button>
              </div>

            </div>

            <!-- Members List -->
            <div class="card memberList">
              <ul class="list-group list-group-flush">

                <!-- If members available -->
                <li v-if="projectItem.userList.length > 0" class="list-group-item memberItem" v-for="member in projectItem.userList">
                  <div class="row">
                    <!-- Member Name -->
                    <div class="col-md-4">{{member.first_name + ' ' + member.last_name}} &nbsp;({{member.email}})</div>

                    <!-- Member Role & Remove Button -->
                    <div class="col-md-8" style="text-align: right;">
                      <el-radio-group v-model="member.role" size="mini" style="margin-right: 40px;" @change="onChangeUserRole(member)">
                        <el-radio-button label="user">User</el-radio-button>
                        <el-radio-button label="admin">Admin</el-radio-button>
                      </el-radio-group>
                      <i class="fa fa-times" style="color: red;" @click="memberToDelete = [member, projectItem]; showDeleteConfirmationPopup = true;"></i>
                    </div>
                  </div>
                </li>

              </ul>
            </div>
          </div>

        </form>

      </el-tab-pane>


    </el-tabs>

    <!-- Delete Member Alert -->
    <el-dialog title="Confirm Delete" :visible.sync="showDeleteConfirmationPopup" width="30%" center>
      <span v-if="memberToDelete">Do you want to remove {{memberToDelete[0].first_name}} form {{memberToDelete[1].name}}?</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showDeleteConfirmationPopup = false">Cancel</el-button>
        <el-button type="danger" @click="onRemoveMemberFromProject(projectItem, member)">Delete</el-button>
      </span>
    </el-dialog>

  </div>
</template>


<style lang="scss">
  #projectDetailsPage {
    padding: 15px;
    padding-top: 30px;
    padding-bottom: 30px;

    .projectContainer {
      border-radius: 5px;
      background: white;
      padding: 20px;

      .addUserBox {
        border: 1px solid #ddd;
        margin: 0px;
        margin-bottom: 20px;
        padding: 15px;
        padding-left: 10px;
        padding-right: 10px;
        font-size: 12px;
        border-radius: 5px;
        color: #555;

        .inputContainer {
          padding-left: 0px;
        }

        input {
          font-size: 12px;
          padding-top: 6px;
          margin-top: 5px;
          padding-bottom: 6px;
          width: 90%;
        }

        .el-radio-button__inner {
          height: 30px;
          padding: 9px 15px;
          margin-top: 5px;
        }

        button {
          margin-top: 5px;
          margin-left: -15px;
        }
      }

      .memberList {
        color: #555;
        font-size: 14px;
        .memberItem {

          label {
            margin-bottom: 0px !important;

          }
        }
      }

    }
  }
</style>

<script>
  import ProjectListComponent from "./projectListComponent";
  export default ProjectListComponent;
</script>


