import router from "../../../router";
import projectService from "../../../services/project";
var _ = require("lodash");
var Joi = require('joi-browser');

export default {
  name: "ProjectList",
  data() {
    return {
      fetchingData: true,
      form: {},
      projectList: [],

      //Fields for project name update
      projectNameMode: 'normal',
      updatingProjectName: false,

      addingNewUser: false,
      newUserErrorMsg: null,
      memberToDelete: null,
      newUserDefaultValues: {
        'role': 'user',
        'isNew': 'true'
      },
      newUserInfo: {
        'role': 'user',
        'isNew': 'true'
      },
      showDeleteConfirmationPopup: false
    };
  },
  methods: {

    addNewUserToProject(project) {

      //Validate new user first.
      this.newUserErrorMsg = null;
      const memberSchema = Joi.object().keys({
        "First Name": Joi.string().alphanum().min(3).max(30).required(),
        "Last Name": Joi.string().alphanum().min(3).max(30).required(),
        "Email Id": Joi.string().email({ minDomainAtoms: 2 }).required()
      });

      var validationResult = memberSchema.validate({
        "First Name": this.newUserInfo.first_name,
        "Last Name": this.newUserInfo.last_name,
        "Email Id": this.newUserInfo.email
      });
      if (validationResult.error) {
        this.newUserErrorMsg = validationResult.error.details[0].message;
        return;
      }

      this.addingNewUser = true;
      let params = {
        first_name: this.newUserInfo.first_name,
        last_name: this.newUserInfo.last_name,
        email: this.newUserInfo.email,
        role: this.newUserInfo.role,
        projectId: project.id,
        invitedBy: this.$store.state.userInfo.first_name  
      };
      console.log('params for new member', params);
      projectService.addProjectMember(params).then((result) => {
        if (result.data.success) {
          console.log('member added successfully', result);
          this.$notify.success('Member added successfully');
          this.newUserInfo = JSON.parse(JSON.stringify(this.newUserDefaultValues));
          this.fetchProjectDetails();
        } else {
          this.$notify.error(result.data.message);
        }
        this.addingNewUser = false;
      }).catch((error) => {
        this.error('Internal Error.');
        this.addingNewUser = false;
      });
    },

    onRemoveMemberFromProject() {


      let project = this.memberToDelete[1];
      let member = this.memberToDelete[0];
      this.showDeleteConfirmationPopup = false;

      //Atleast one member required.
      if (project.userList.length <= 1) {
        this.$notify.error('Atleast one member required in project.');
        return;
      }

      let params = {
        userId: member.id,
        projectId: project.id
      };


      console.log('params for remove member', params);
      projectService.removeProjectMember(params).then((result) => {
        console.log('member removed successfully', result);
        this.$notify.success('Member removed successfully.');
        this.fetchProjectDetails();
      }).catch((error) => {
        console.error('failed to remove member', error);
      });
    },

    onChangeUserRole(user) {
      let params = {
        userId: user.id,
        role: user.role
      }

      projectService.updateMemberRole(params).then((result) => {
        this.$notify.success('Member role updated.');
      }).catch((error) => {
        this.$notify.error('Failed to upate member role.');
      });
    },

    onUpdateProjectName(project) {
      console.log('orignal', project.originalName, 'new', project.name);

      //Validate new user first.
      if (project.name.trim() == '') {
        this.$notify.error('Please provide valid project name');
        return;
      } else if (project.name.length > 30) {
        this.$notify.error('Project name can not exceed 30 characters.');
        return;
      } else {
        let regex = /^[a-zA-Z0-9\s]+$/;
        if (!regex.test(project.name)) {
          this.$notify.error('Only characters & numbers allowed for Project name.');
          return;
        }
      }

      // Update name on server
      this.updatingProjectName = true;
      var params = { projectId: project.id, newProjectName: project.name };

      try {
        let result = projectService.updateProjectName(params).then((response) => {
          if (response.data.success) {
            this.$notify.success('Project name updated.');
            this.projectNameMode = 'normal';
            this.updatingProjectName = false;

            //Update project name in store.
            let userInfo = this.$store.state.userInfo;
            for (var i = 0; i < this.$store.state.userInfo.projectList.length; i++) {
              if (this.$store.state.userInfo.projectList[i].projectCode == project.code) {
                this.$store.state.userInfo.projectList[i].name = project.name;
              }
            }
            this.$store.dispatch("setUserInfo", userInfo);

          } else {
            this.$notify.error(response.data.message);
            this.updatingProjectName = false;
          }
        }).catch((err) => {
        });

      } catch (err) {
        this.error('Internal Error.');
        this.updatingProjectName = false;
      }



    },

    fetchProjectDetails() {

      this.fetchingData = true;
      projectService.getProjectList()
        .then(result => {
          if (result.data.success) {
            this.projectList = result.data.projectList;
            for (var i = 0; i < this.projectList.length; i++) {
              this.projectList[i].originalName = this.projectList[i].name;
            }
            this.fetchingData = false;
          } else {
            this.$toast.error(result.data.message);
            this.fetchingData = false;
          }
        })
        .catch(error => {
          this.error('Internal Error.');
          this.fetchingData = false;
        });
    },

  },
  created() {
    this.fetchProjectDetails();
  }
};



